body {
    font-size: 16px;
    line-height: 24px;
    color: $wh;
    background-color: $wh;

    @extend %jeko-400;
}

.container {
    margin: 0 auto;
    padding: 0 40px;
    max-width: 1568px;
}

.container--small {
    margin: 0 auto;
    padding: 0 40px;
    max-width: 1264px;
}

.fact {
    padding: 156px 0;

    &__text {
        margin: 0 auto;
        max-width: 968px;
        font-size: 24px;
        line-height: 36px;
        text-align: center;
        color: $bl;

        @include r($lg) {
            font-size: 30px;
        }

        @include r($md) {
            font-size: 20px;
            line-height: 32px;
            text-align: left;
        }

        @extend %jeko-700;
    }

    @include r($lg) {
        padding: 50px 0 100px;
    }
}